.container {
  text-align: center;
  overflow: hidden;
  color: white;
  width: 500px;
  margin: auto;
  margin-top: 20%;
  transition: margin-top 1s;
  }

  @media (max-width: 500px) {
    .container {
      text-align: center;
      overflow: hidden;
      color: white;
      width: 100%;
      margin: auto;
      margin-top: 50%;
      transition: margin-top 1s;
      
    }
  }